<template>
  <div class="collection">
    <div class="rg-top" style="margin:0 10px;">
      <div class="rg-top-top">
        <span class="content-top-text-blue"></span>
        <span style="margin-left: 2px">{{company[0].shenbao_month}} 税款缴纳</span>
      </div>
      <div class="rg-top-bottom">
        <div>
          <el-button type="primary" size="small" @click="jiaokuanClick">立即缴款</el-button>
          <el-button type="primary" size="small" @click="jiaokuanFKClick">缴纳反馈</el-button>
          <el-button type="primary" size="small">打印</el-button>
        </div>
      </div>
    </div>
    <div class="rg-top" style="margin:10px">
      <el-form :inline="true" class="demo-form-inline" label-width="80px">
        <el-form-item label="税款所属期">
          <el-date-picker type="date" placeholder="选择日期" style="width: 48%;" value-format="yyyy-MM-dd"></el-date-picker>
          -
          <el-date-picker type="date" placeholder="选择日期" style="width: 48%;" value-format="yyyy-MM-dd"></el-date-picker>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small">查询</el-button>
          <el-button type="primary" size="small">重置</el-button>
        </el-form-item>
      </el-form>
    </div>

    <div v-if="shenBaoStatus" style="margin: 0 10px;">
      <div id="con_in" class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="threeTable" border style="max-width:100%">
          <!-- <el-table-column type="selection" disabled width="55"></el-table-column> -->
          <el-table-column label="所得月份">
            <template>
              {{ company[0].shenbao_month }}
            </template>
          </el-table-column>
          <el-table-column prop label="申报表">个人扣缴所得税</el-table-column>
          <el-table-column prop="incomeProject" label="征收品目"></el-table-column>
          <el-table-column prop="taxAll.shuilv" label="税率"></el-table-column>
          <el-table-column prop="address" label="税款所属期起">
            <template>
              {{ company[0].time_start }}
            </template>
          </el-table-column>
          <el-table-column prop="address" label="税款所属期止">
            <template>
              {{ company[0].time_end }}
            </template>
          </el-table-column>
          <el-table-column prop="tuibu" label="应补（退）税额"></el-table-column>
          <el-table-column prop="address" label="缴款期限">
            <template>
              {{ parseInt(company[0].time_end.substr(5, 2)) >= 12 ? company[0].time_end.substr(0, 5) + '01' : company[0].time_end.substr(0, 5) + (parseInt(company[0].time_end.substr(5, 2)) + 1) }}
            </template>
          </el-table-column>
        </el-table>
      </div>
    </div>
    <div v-else style="margin: 0 10px;">
      <div class="con-div" style="overflow-y: auto; display: block;">
        <el-table :data="jiaokuanForm" border style="max-width:100%">
          <el-table-column label="所得月份">
            <template>
              {{ company[0].shenbao_month }}
            </template>
          </el-table-column>
          <el-table-column prop label="申报表">个人扣缴所得税</el-table-column>
          <el-table-column prop="address" label="税款所属期起">
            <template>
              {{ company[0].time_start }}
            </template>
          </el-table-column>
          <el-table-column prop="address" label="税款所属期止">
            <template>
              {{ company[0].time_end }}
            </template>
          </el-table-column>
          <el-table-column prop="tuibu" label="应补（退）税额"></el-table-column>
          <el-table-column prop="address" label="缴款结果">
            缴款成功
          </el-table-column>
        </el-table>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters } from 'vuex'

export default {
  components: {},
  data () {
    return {
      shenBaoStatus: true
    }
  },
  computed: {
    ...mapGetters(['company']),
    threeTable () {
      return this.$store.state.form.shenbaoForm
    },
    jiaokuanForm () {
      return this.$store.state.form.jiaokuanForm
    }
  },
  created () {
    this.$emit('router', this.$router.history.current.name)
    console.log(this.threeTable)
  },
  mounted () {

  },
  methods: {
    jiaokuanClick () {
      if (this.threeTable.length === 0) {
        this.$message.warning('没有申报的项目缴款，请先去申报')
      } else {
        this.$confirm(`确定要对所有的${this.threeTable.length}条税务进行缴款`, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$store.commit('upJiaokuan')
          this.$message({
            type: 'success',
            message: '缴款成功!'
          })
        }).catch((e) => {
          this.$message({
            type: 'info',
            message: '已取消缴款'
          })
        })
      }
    },
    jiaokuanFKClick () {
      this.shenBaoStatus = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
  h1,
  h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: block;
    margin: 0;
  }

  a {
    color: #42b983;
  }

  img {
    vertical-align: middle;
  }

  .collection {
    font-size: 12px;
    text-align: start;
  }

  .hot-notice {
    border: solid 1px #d2e7fb;
    background-color: #eaf4ff;
    box-sizing: border-box;
    margin: 10px 35px 0 10px;
    padding: 8px;
    line-height: 20px;
    text-align: left;
  }

  .rg-top {
    border: solid 1px #e0e0e0;
    padding: 0 10px;
    margin: 0 35px 0 10px;
    font-size: 16px;
    font-weight: bold;
    color: #000000;
  }

  .rg-top-top {
    height: 40px;
    overflow: hidden;
    padding: 0 10px;
    line-height: 40px;
    box-sizing: border-box;
  }

  .rg-top-bottom {
    height: 40px;
    padding: 0 10px;
    line-height: 30px;
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
  }

  .right-btn {
    padding: 8px 10px 8px 10px;
    border: 1px #5a98de solid;
    border-radius: 5px;
    font-size: 14px;
  }

  .right-btn:hover {
    background-color: rgb(64, 158, 255);
    color: white;
  }

  .con_btn1 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid #4586f5;
    border-width: 0px 0px 2px 0px;
    font-size: 15px;
    color: #4586f5;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .con_btn2 {
    float: left;
    width: 112px;
    height: 33px;
    line-height: 33px;
    border: solid transparent;
    border-width: 0 0 2px 0;
    font-size: 15px;
    color: #000000;
    text-align: center;
    background-color: #ffffff;
    cursor: pointer;
  }

  .in-table .notice {
    display: block;
    width: 100%;
    height: 20px;
    /* display: block; */
    background-color: rgb(212, 244, 252);
  }

  .in-table-content {
    padding: 0 30px;
  }

  .rg-top .el-form-item {
    margin: 10px 0;
  }
</style>
<style>
  .el-button + .el-button {
    margin: 0;
  }

  .el-dialog__body {
    padding: 0;
  }

  .person-status .el-form-item__label {
    position: relative;
    float: right;
    right: 180px;
  }

  .person-status.el-form-item {
    margin-bottom: 5px;
  }

  [data-v-130d4e25] .el-radio-group {
    float: right;
    position: relative;
    right: -140px;
  }

  .el-form-item {
    margin-bottom: 18px;
  }

  [data-v-130d4e25] .el-radio__input {
    display: none;
  }

  [data-v-130d4e25] .el-radio.el-radio--medium {
    margin-right: 10px;
  }

  .el-form-item__label {
    width: 120px;
  }

  .el-select {
    width: 100%;
  }

  .el-form-item__label {
    font-size: 12px;
  }
</style>



